const getControllers = (dispatches, asset_type, existingControls) => {
  let controllers = {
    onrc: {
      title: "ONRC",
      values: [...new Set(dispatches.map((d) => d.onrc).sort())],
      controls: {},
      canApply: true,
    },
    days_since: {
      title: "Days Since",
      values: [...new Set(dispatches.map((d) => d.days_since).sort())],
      controls: {},
      canApply: true,
    },
    asset_type_desc: {
      title: "Asset Type",
      values: [...new Set(dispatches.map((d) => d.asset_type_desc).sort())],
      controls: {},
      canApply: false,
    },
    fault_type: {
      title: "Fault Type",
      values: [...new Set(dispatches.map((d) => d.fault_desc).sort())],
      controls: {},
      canApply: false,
    },
    priority_desc: {
      title: "Priority",
      values: [...new Set(dispatches.map((d) => d.priority_desc).sort())],
      controls: {},
      canApply: true,
    },
    call_type_desc: {
      title: "Call Type",
      values: [...new Set(dispatches.map((d) => d.call_type_desc).sort())],
      controls: {},
      canApply: true,
    },
    programme_category_desc: {
      title: "Programme Category",
      values: [
        ...new Set(dispatches.map((d) => d.programme_category_desc).sort()),
      ],
      controls: {},
      canApply: true,
    },
    programme_desc: {
      title: "Programme Name",
      values: [...new Set(dispatches.map((d) => d.programme_desc).sort())],
      controls: {},
      canApply: true,
    },
    pave_type: {
      title: "Pavement Type",
      values: [...new Set(dispatches.map((d) => d.pave_type).sort())],
      controls: {},
      canApply: true,
    },
  
  };

  // read from local storage the existing controls
  existingControls.forEach(c => {
    //const existing = localStorage.getItem(`${asset_type}-controls-${c}`);
    //const output = existing ? JSON.parse(existing) : {};
    let controls = {};

    c.name_list.forEach((d,i) => {
      controls = {...controls, [d] : c.score_list[i]}
    })

    controllers[c.type].controls = controls;
  })

  return controllers;
};

export default getControllers;
